    const styleGuide = 
`> __**Introduction**__
.tag:introduction
__**About the Style Guide**__
⬥ This channel details many stylistic aspects that all PvME guides should follow.
⬥ If submitting a write up, please follow the guidelines in this channel.
⬥ These guidelines do not cover every situation, and will be amended as necessary.
    • Please bring up any concerns that are not covered by these guidelines.

.
> __**General**__
.tag:general
**__Random Stylistic Choices We Made__**
⬥ Utilise UK spelling and style (armour over armor).
⬥ Dates will be written in the format: YYYY/MM/DD.
⬥ Every channel should have its channel topic containing the correctly captialised title ("aod" channel would have "Angel of Death" as it's topic).
⬥ Capitalize the first letter of every line.
⬥ Where possible, the first message of a guide should be an image that best represents the guide.
⬥ Utilise the Oxford Comma.
⬥ If something makes a complete sentence, remember to add a period at the end.
⬥ If crediting a user for a section of a guide, or the entire guide, do \`Title (Courtesy of @user)\`.
⬥ When using emojis as not part of a rotation, do word, then emoji.
    • The only exception to this rule is in presets where obscure items go emoji, then word.
⬥ **Bold**: The only time you will just bold something is if it is a main item to be described from a list.
    • Example: The part in the red box below indicates what should be bold.
.
.img:https://i.imgur.com/zAUPDb3.png
.
⬥ *Notes: Should be italicized* and typically is not preceded by a bullet point.
    • Example: The part in the red box below indicates what a note should look like in github.
.
.img:https://i.imgur.com/Nl5qKod.png
.
> **__Embedding Images/Videos__**
.tag:embed
**__Formatting Embeds__**
⬥ It is preferred that videos are hosted on Youtube. Streamable is absolutely forbidden.
⬥ Images and video embeds should be their own message, no links to images in guides. Imgur is the preferred hosting platform.
    • Example: The part in the red box below indicates how to format an embed in github.
.
.img:https://i.imgur.com/pnHUaiY.png
.
⬥ Website links should be formatted as such: \`**Link text** - <https://...>\`
    • Example: **RuneScape Wiki** - <https://runescape.wiki/>
        - Surround links with < and > to prevent embeds from appearing.
⬥ If an empty line is required to split sections at a message boundary, place the empty line at the end of the first message.

.
> __**Sections and Subsections**__
.tag:sections
**__Formatting Sections__**
⬥ Add a .tag command one line below the section title to link Table of Contents to the section title like this: \`.tag:word\`.
⬥ Format Sections like this: \`> __**Sections**__\`.
⬥ Format Subsections like this: \`__**Subsection**__\`.
⬥ Every Section should have a subsection (the only exception is a \`Drops\` section).
    • If you can't think of a good subsection title, you can say __**General x for y**__.
    • Words in Subsections should follow the same capitalization guidelines as Sections.
⬥ Example: The picture below indicates how to format a section/subsection in github.
.
.img:https://i.imgur.com/6ovG30i.png
.
> __**Lists**__
.tag:lists
__**Bulleted Lists**__
⬥ Format main points like this: \`⬥\` with 0 spaces before, 1 space after the ⬥.
    • Format subpoints like this: \`•\` with 4 spaces before, 1 space after the •.
        - Format subsubpoints like this: \`-\` with 8 spaces before, 1 space after the -.
__**Alphanumerical Lists**__
1. Format main points like this: \`appropriate # followed by a period\` with 0 spaces before, 1 space after the number.
    a. Format subpoints like this: \`appropriate letter followed by a period\` with 4 spaces before, 1 space after the letter.
        - Format subsubpoints like this: \`-\` with 8 spaces before, 1 space after the -.
    b. __**Bold and underline lists when they act as titles**__.
⬥ Example: The picture below indicates how to format bulleted lists in github.
.
.img:https://i.imgur.com/fXBFQZl.png
.
> __**Presets**__
.tag:presets
**__Preset Rules__**
⬥ Rune Pouches in presets should match colours of pouches in <#689235882724818962>.
⬥ EoF emojis, when possible, should match colours in <#797160458980950029>.
⬥ Weapon switches should use the same switches in <#553632787639435284> (e.g. cywir represents planted feet).
⬥ Avoid using dyed gear in presets.
⬥ Describe lesser known items and rune pouch/quiver/EoF contents below the preset.
    • Rune Pouches should be formatted as \`<pouchEmoji> <rune1> <rune2> ...\`.
    • EoFs should be formatted as \`<eofEmoji> with <weaponName> <weaponEmoji>\`.
        - A list of common EoF combinations, in preset format, can be found in <#856674081789181953>.
    • Example with corresponding raw text:
.
